/* reset css */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
  font-family: "Karla", sans-serif;
  font-size: 1.6rem;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Custom Additions Below */
:root, * {
  box-sizing: border-box;
}

:root {
  font-size: 62.5%;
}

a, a:hover, a:focus, a:active {
  text-decoration: none;
  color: inherit;
}

button {
  font-family: "Karla", sans-serif;
  letter-spacing: 0.2rem;
  text-transform: uppercase;
  color: #fafafa;
  border: 0.19rem solid #fafafa;
  outline: none;
  background: none;
  cursor: pointer;
}

h1, h3 {
  font-family: "DM Serif Display", sans-serif;
  font-weight: 500;
}

p {
  line-height: 1.7;
}

body {
  width: 100%;
  height: 100vh;
  /* header style */
  /* intro & how we work*/
}
body header {
  background-color: #fafafa;
}
body header .container {
  padding-top: 3rem;
}
body header .container nav {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
body header .container nav img {
  margin-left: 2.5rem;
  display: inline-block;
  width: 11.1rem;
  height: 2rem;
}
body header .container nav img, body header .container nav button {
  margin-bottom: 2.5rem;
}
body header .container nav button {
  margin-right: 2.5rem;
  display: inline-block;
  width: 3rem;
  height: 3rem;
  color: #837d87;
  outline: 0.2rem solid #837d87;
}
body header .container nav button#times {
  display: none;
}
body header .container nav ul {
  position: absolute;
  top: 12%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4rem;
  padding: 5.4rem 2.4rem 29.9rem 2.4rem;
  width: 100%;
  background-image: url(/images/bg-pattern-mobile-nav.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position-y: bottom;
  background-color: #2b272f;
  font-size: 2rem;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  color: #fafafa;
  display: none;
}
body header .container nav ul.show {
  display: flex;
}
body header .container nav ul li:last-of-type {
  padding: 1.6rem 10rem;
  border: 0.1rem solid #fafafa;
  white-space: nowrap;
}
body .intro-how-we-work-container img.desktop-intro {
  display: none;
}
body .intro-how-we-work-container .intro-how-we-work-inner-container {
  background-image: url("/images/bg-pattern-intro-right-mobile.svg");
  background-repeat: no-repeat;
  background-position: top 34.1rem right 0.1rem;
}
body .intro-how-we-work-container .intro-how-we-work-inner-container .intro-container {
  display: flex;
  flex-direction: column;
  gap: 1.7rem;
  margin-top: -0.4rem;
  padding: 9rem 2.4rem 9.4rem 2.4rem;
  text-align: center;
  background-image: url(/images/bg-pattern-intro-left-mobile.svg), url(/images/bg-pattern-intro-right-mobile.svg);
  background-repeat: no-repeat;
  background-position: left top, bottom -18rem right -0.5144rem;
  background-color: #2d2640;
}
body .intro-how-we-work-container .intro-how-we-work-inner-container .intro-container hr {
  display: none;
}
body .intro-how-we-work-container .intro-how-we-work-inner-container .intro-container h1 {
  font-size: 4.5rem;
  color: #fafafa;
}
body .intro-how-we-work-container .intro-how-we-work-inner-container .intro-container p {
  color: #fafafa;
  opacity: 0.7;
}
body .intro-how-we-work-container .intro-how-we-work-inner-container .intro-container a button {
  margin-top: 1.2rem;
  padding: 1rem 2.7rem;
  color: #fafafa;
}
body .intro-how-we-work-container .intro-how-we-work-inner-container .how-we-work-container {
  padding: 0 2.7rem;
  text-align: center;
}
body .intro-how-we-work-container .intro-how-we-work-inner-container .how-we-work-container hr {
  margin: 14.5rem 0 4.5rem 13rem;
  border: 0;
  height: 0.2rem;
  width: 40%;
  background: #ccc;
}
body .intro-how-we-work-container .intro-how-we-work-inner-container .how-we-work-container h1 {
  font-size: 4.5rem;
}
body .intro-how-we-work-container .intro-how-we-work-inner-container .how-we-work-container .info {
  margin-top: 7.6rem;
  display: flex;
  flex-direction: column;
  gap: 6.6rem;
}
body .intro-how-we-work-container .intro-how-we-work-inner-container .how-we-work-container .info .info-details {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2.7rem;
}
body .intro-how-we-work-container .intro-how-we-work-inner-container .how-we-work-container .info .info-details img {
  display: inline-block;
  width: 8.6rem;
  height: 8.6rem;
}
body .intro-how-we-work-container .intro-how-we-work-inner-container .how-we-work-container .info .info-details h3 {
  margin-top: 0.4rem;
  font-size: 2.7rem;
  color: #2d2640;
}
body .intro-how-we-work-container .intro-how-we-work-inner-container .how-we-work-container .info .info-details p {
  color: #837d87;
}
body .intro-how-we-work-container .intro-how-we-work-inner-container .how-we-work-container .find-out-more {
  margin-top: 14.5rem;
  padding: 7.5rem 4.1rem 6.5rem 4.1rem;
  background-image: url(/images/bg-pattern-how-we-work-mobile.svg);
  background-repeat: no-repeat;
  background-position-x: right;
  background-color: #2d2640;
  color: #fafafa;
}
body .intro-how-we-work-container .intro-how-we-work-inner-container .how-we-work-container .find-out-more h1 {
  font-size: 3.85rem;
}
body .intro-how-we-work-container .intro-how-we-work-inner-container .how-we-work-container .find-out-more a button {
  margin-top: 4.6rem;
  padding: 1rem 2.3rem;
  color: #fafafa;
}

